.footer__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  margin-top: auto;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;

  .footer {
    &__logo {
      width: 40px;
    }
    &__social-media {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 5px;
    }
  }
}
